// IMPORTS
// Material UI
import AppBar from '@material-ui/core/AppBar'
import Dialog from '@material-ui/core/Dialog'
import IconButton from '@material-ui/core/IconButton'
import Slide from '@material-ui/core/Slide'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/core/styles/withStyles'
import CloseIcon from '@material-ui/icons/Close'
import PropTypes from 'prop-types'

// Externals imports (Dependencies)
import React from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'

import styles from '../../../resources/theme/distributionCenters'

// Internal imports
import EnhancedComponent from '../../components/EnhancedComponent'
import { edit, fetchDistributionCenters, update } from '../DistributionCentersActions'
import { getEditingDistributionCenters } from '../DistributionCentersReducer'
import Form from './Form'

function Transition(props) {
  return <Slide direction="down" {...props} />
}

class EditDistributionCenter extends EnhancedComponent {
  state = {
    fields: {
      description: '',
      CNPJ: '',
      CEP: '',
      street: '',
      number: '',
      complement: '',
      bairro: '',
      city: '',
      state: '',
    },
  }

  componentDidMount() {
    this.props.edit(this.props.match.params.id)
  }

  onSubmit = values => {
    const { fetchDistributionCenters } = this.props
    // valida os campos se necessário
    this.props.update(values).then(data => {
      fetchDistributionCenters()
      if (data && data.code === 200) {
        this.props.history.push('/distributioncenters')
      }
    })
  }

  onClose = () => {
    this.props.history.push('/distributioncenters')
  }

  render() {
    const { classes, distributionCenter } = this.props
    return (
      <Dialog
        fullScreen
        open={distributionCenter !== null}
        onClose={this.onClose}
        TransitionComponent={Transition}
      >
        <AppBar className={classes.appBar}>
          <Toolbar>
            <Typography variant="h6" color="inherit" className={classes.flex}>
              Editar Centro de Distribuição
            </Typography>
            <IconButton
              color="inherit"
              onClick={this.onClose}
              aria-label="Close"
            >
              <CloseIcon />
            </IconButton>
          </Toolbar>
        </AppBar>

        <Form
          data={distributionCenter}
          onClose={this.onClose}
          onSubmit={this.onSubmit}
        />
      </Dialog>
    )
  }
}

EditDistributionCenter.propTypes = {
  classes: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
  roles: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  distributionCenter: getEditingDistributionCenters(state),
})

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      update,
      edit,
      fetchDistributionCenters,
    },
    dispatch
  )

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(EditDistributionCenter))
